import * as React from "react";
import { Component } from "react";
import "./App.css";
import TestDrag from "./components/TestDrag";
import { Slider } from "./slider";
import WithOnResize from "./slider/utilities/WithOnResize";

import logo from "./logo.svg";

const url =
	"https://i2.wp.com/beebom.com/wp-content/uploads/2016/01/Reverse-Image-Search-Engines-Apps-And-Its-Uses-2016.jpg?resize=640%2C426";

class App extends Component {
	public render() {
		return (
			<div className="App">
				<header className="App-header">
					<img src={logo} className="App-logo" alt="logo" />
					<h1 className="App-title">Welcome to React</h1>
				</header>
				<p className="App-intro">
					To get started, edit <code>src/App.tsx</code> and save to reload.
				</p>
				<TestDrag />
				<WithOnResize
					widthFactor={1}
					heightFactor={2 / 3}
					useWidthForHeight={true}
				>
					{(width: number, height: number) => (
						<Slider
							width={`${width}px`}
							height={`${height}px`}
							images={[
								"data:image/jpeg;base64,",
								"data:image/jpeg;base64,",
								url
							]}
						/>
					)}
				</WithOnResize>
			</div>
		);
	}
}

export default App;



